@import "mixins";


@media (min-width: 992px) {
	.navbar-expand-lg .navbar-collapse {
		display: -webkit-box!important;
		display: -ms-flexbox!important;
		display: flex!important;
		-ms-flex-preferred-size: auto;
		flex-basis: auto; } }

@media (min-width: 992px) {
	.navbar-expand-lg .navbar-toggler {
		display: none; } }

@media (min-width: 992px) {
	.navbar-expand-lg .navbar-nav {
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		-ms-flex-direction: row;
		flex-direction: row; } }

@media (min-width: 992px) {
	.navbar-expand-lg {
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		-ms-flex-flow: row nowrap;
		flex-flow: row nowrap;
		-webkit-box-pack: start;
		-ms-flex-pack: start;
		justify-content: flex-start; } }

@media (min-width: 992px) {
	.navbar-expand-lg .navbar-nav .nav-link {
		padding-right: .5rem;
		padding-left: .5rem; } }


@media (min-width: 576px) {
	.container {
		max-width: 100% !important; } }

@media (max-width: 992px) {
	.main_page_left_column,
	.main_page_right_column {
		width: 100% !important;
		max-width: 100% !important;
		flex: 0 0 100%; }
	.sidebar_offer {
		display: none; } }

@media (max-width: 992px) {

	.menu_button {
		display: block;
		outline: none !important; }
	.navbar {
		position: absolute;
		background: $main-blue;
		// background: #000
		width: 100%;
		// height: 0
		padding: 0;
		top: 100%;
		left: 0;
		z-index: 1000; }
	// .collapse.show
	/// width: 100%
	.nav-item {
		padding: 15px;
		width: 100%; }

	.main_content {
		padding-left: 5px;
		padding-right: 5px; }
	.footer_menu {
		flex-wrap: wrap;
		.footer_block_3 {
			margin-right: 20%; }
		.footer_block_4 {
			width: 100%;
			max-width: none;
			margin-top: 15px;
			* {
				display: inline-block;
				vertical-align: middle; }
			.footer_logo {
				margin-right: 30px;
				margin-bottom: 0; }
			.footer_block_text {
				width: calc(100% - 190px);
				p {
					margin: 0; } } } } }

@media (max-width: 768px) {
	.popular_product {
		.popular_product_cart:nth-child(9),
		.popular_product_cart:nth-child(5), {
			display: none; }
		.popular_product_cart {
			width: 30.2%; } } }

@media (max-width: 715px) {

	.navbar_contacts {
		div {
			display: block; }
		.navbar_contacts_phone {
			margin: 0;
			text-align: center; } }

	.main_content {
		.main_page_slider_hit {
			justify-content: center;
			.slider_block {
				padding-right: 0;
				width: 100%;
				max-width: 100%;
				min-width: auto;
				margin-bottom: 30px;
				// .slick-slide
				// 	img
				// 		width: 100%
 } } }				// 		min-height: 100% !important
	.we_are {
		flex-direction: column;
		align-items: center;
		.we_are_block {
			width: 100%; } }
	.baner {
		display: none; }
	.about_shop {
		margin-top: 5px;
		.about_shop_block {
			flex-direction: column;
			.about_shop_block_img {
				text-align: center;
				margin: 15px 0;
				img {
					top: auto;
					left: auto;
					@include transform(translate(0%, 0%)); } } } } }

// @media (max-width: 665px)


@media (max-width: 625px) {
	.popular_product {
		.popular_product_cart {
			width: 48%;
			.popular_product_cart_img {
				img {
					// height: 100%
					height: auto;
					width: auto;
					max-width: 100%; } } } }

	.footer_menu {
		.footer_block {
			width: 100%; } } }

@media (max-width: 576px) {
	.container {
		max-width: 100% !important; }
	.header_first_block {
		display: none; } }

@media (max-width: 576px) {
	.footer_menu {
		.footer_block_4 {
			.footer_block_text {
				width: 100%;
				margin-top: 20px; } } } }

@media (max-width: 520px) {
	.nav_container {
		flex-wrap: wrap;
		.navbar_contacts {
			width: 100%;
			text-align: center;
			margin-bottom: 15px;
			.navbar_contacts_promo {
				max-width: 280px;
				margin: auto; } } } }


@media (max-width: 480px) {
	.popular_product {
